.overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgb(255, 255, 255);
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.block {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}

.logo {
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.3rem;
  user-select: none;
  max-width: 213px;
  margin: auto;
}

.logo img{
  width: 10rem;
  height: auto;
  
}

.spin {
  border: 5px solid #7a0185b7; /* Light grey */
  border-top: 5px solid #eb0117;
  border-bottom: 5px solid #1200ee;
  border-radius: 50%;
  margin: auto;
  width: 1.5rem;
  height: 1.5rem;
  animation: spin 1s linear infinite;
  margin-top: -10px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

p {
  margin-top: 0;
  font-size: 22.45px;
  font-weight: 100;
  text-align: center;
}
