

.descriptionDiv {
  background-color: rgb(17, 159, 26);
  width: 100%;
  color: white;
  display: flex;
  padding-top: 20px;
  flex-direction: column;
}

.descriptionDiv h1 {
  text-align: center;
  margin: auto;
  font-size: 1.5rem;
}

p {
  align-self: center;
}

.circlesDiv {
  justify-content: space-between;
  margin: 2rem 0;
}

.circlesDiv div {
  margin: 0 1.5rem;
  margin: auto;
}

.circle {
  height: 50vw;
  width: 50vw;
  max-width: 170px;
  max-height: 170px;
  background-color: white;
  border-radius: 50%;
  margin: auto;
}

.circleDesc {
  width: 50vw;
  margin: auto;
  margin-bottom: 2rem;
  margin-top: 1rem;
}

.newsLetterSection {
  background-color: rgb(0, 102, 255);
  width: 100%;
}

.newsLetterSection form {
  display: flex;
  flex-direction: column;
}

.newsLetterSection h1 {
  text-align: center;
  color: white;
  font-size: 1.2rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.newsLetterSection input[type="text"] {
  width: 60vw;
  max-width: 290px;
  height: 2rem;
  border: none;
  border-radius: 10px;
  padding: 10px;
  margin: auto;
}

.newsLetterSection input[type="submit"] {
  width: 6rem;
  height: 2.2rem;
  margin: auto;
  margin-bottom: 2rem;
  border-radius: 10px;
  border: none;
  font-size: 1rem;
}

.newsLetterSection input[type="submit"]:hover {
  background-color: rgb(156, 155, 153);
}

.newsLetterSection input[type="checkbox"] {
  font: inherit;
  color: currentColor;
  width: 1.15em;
  height: 1.15em;
  border: 0.15em solid currentColor;
  border-radius: 0.15em;
  transform: translateY(-0.075em);
  align-self: center;
}

.checkboxSection {
  display: flex;
  justify-content: center;
  border-radius: 8px;
  margin: 0.4rem auto;
}

.checkboxSection p {
  margin-right: 1rem;
  font-size: 1rem;
  color: white;
}
